* {
  box-sizing: border-box;
}

body {
  --maincolor: #00acdc;
  margin: 0;
  font-family: Roboto, sans-serif;
}

.header {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  min-width: 1024px;
  margin: 0 auto;
  padding: 0 50px;
  display: flex;
}

.logo--image {
  display: block;
}

.menu {
  align-items: center;
  margin: 0;
  padding: 0;
  display: flex;
}

.menu--li {
  height: 60px;
  margin: 0 20px 0 0;
  list-style: none;
}

.menu--li:last-child {
  margin-right: 0;
}

.menu-link.is-active:after {
  content: "";
  background-color: var(--maincolor);
  z-index: 1;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}

.menu-link {
  text-transform: uppercase;
  color: #000;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: inline-block;
  position: relative;
}

.menu-link:hover, .menu-link.is-active {
  color: var(--maincolor);
}
